import { registerPlugin } from '@wordpress/plugins';
import { PluginSidebar, PluginSidebarMoreMenuItem } from '@wordpress/editor';
import { TextControl, TextareaControl, PanelBody, Button, BaseControl } from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Fragment } from '@wordpress/element';

const SeoTab = () => {
  const meta = useSelect(
    (select) => select('core/editor').getEditedPostAttribute('meta'),
    []
  );

  const { editPost } = useDispatch('core/editor');

  const updateMeta = (field, value) => {
    editPost({
      meta: {
        ...meta,
        [field]: value,
      },
    });
  };

  const onSelectImage = (field) => (image) => {
    updateMeta(field, image.url);
  };

  const onRemoveImage = (field) => () => {
    updateMeta(field, '');
  };

  const ImageControl = ({ label, value, help, field }) => (
    <BaseControl label={label} help={help}>
      <div style={{ marginBottom: '8px' }}>
        {value && (
          <div style={{ marginBottom: '8px' }}>
            <img
              src={value}
              alt="Preview"
              style={{
                maxWidth: '100%',
                height: 'auto',
                maxHeight: '150px',
                display: 'block'
              }}
            />
          </div>
        )}
        <MediaUploadCheck>
          <MediaUpload
            onSelect={onSelectImage(field)}
            allowedTypes={['image']}
            value={value}
            render={({ open }) => (
              <Fragment>
                <Button
                  variant="secondary"
                  onClick={open}
                  style={{ marginRight: '8px' }}
                >
                  {value ? '–ó–∞–º–µ–Ω–∏—Ç—å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ' : '–í—ã–±—Ä–∞—Ç—å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ'}
                </Button>
                {value && (
                  <Button
                    variant="tertiary"
                    isDestructive
                    onClick={onRemoveImage(field)}
                  >
                    –£–¥–∞–ª–∏—Ç—å
                  </Button>
                )}
              </Fragment>
            )}
          />
        </MediaUploadCheck>
      </div>
    </BaseControl>
  );

  return (
    <>
      <PluginSidebarMoreMenuItem target="seo-sidebar">
        –°–ï–û
      </PluginSidebarMoreMenuItem>

      <PluginSidebar
        name="seo-sidebar"
        title="SEO –ù–∞—Å—Ç—Ä–æ–π–∫–∏"
        icon="admin-site"
      >
        <PanelBody>
          <TextareaControl
            label="SEO Description"
            value={meta._seo_description || ''}
            onChange={(value) => updateMeta('_seo_description', value)}
            help="–ö—Ä–∞—Ç–∫–æ–µ –æ–ø–∏—Å–∞–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –¥–ª—è –ø–æ–∏—Å–∫–æ–≤—ã—Ö —Å–∏—Å—Ç–µ–º"
          />
          <TextControl
            label="SEO Keywords"
            value={meta._seo_keywords || ''}
            onChange={(value) => updateMeta('_seo_keywords', value)}
            help="–ö–ª—é—á–µ–≤—ã–µ —Å–ª–æ–≤–∞ —á–µ—Ä–µ–∑ –∑–∞–ø—è—Ç—É—é"
          />

          {/* Open Graph */}
          <TextControl
            label="OG Title"
            value={meta._og_title || ''}
            onChange={(value) => updateMeta('_og_title', value)}
            help="–ó–∞–≥–æ–ª–æ–≤–æ–∫ –¥–ª—è Facebook –∏ –¥—Ä—É–≥–∏—Ö —Å–æ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å–µ—Ç–µ–π"
          />
          <TextareaControl
            label="OG Description"
            value={meta._og_description || ''}
            onChange={(value) => updateMeta('_og_description', value)}
            help="–û–ø–∏—Å–∞–Ω–∏–µ –¥–ª—è —Å–æ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å–µ—Ç–µ–π"
          />
          <ImageControl
            label="OG Image"
            value={meta._og_image || ''}
            help="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –¥–ª—è —Å–æ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å–µ—Ç–µ–π"
            field="_og_image"
          />

          {/* Twitter */}
          <TextControl
            label="Twitter Title"
            value={meta._twitter_title || ''}
            onChange={(value) => updateMeta('_twitter_title', value)}
            help="–ó–∞–≥–æ–ª–æ–≤–æ–∫ –¥–ª—è Twitter"
          />
          <TextareaControl
            label="Twitter Description"
            value={meta._twitter_description || ''}
            onChange={(value) => updateMeta('_twitter_description', value)}
            help="–û–ø–∏—Å–∞–Ω–∏–µ –¥–ª—è Twitter"
          />
          <ImageControl
            label="Twitter Image"
            value={meta._twitter_image || ''}
            help="–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –¥–ª—è Twitter"
            field="_twitter_image"
          />
        </PanelBody>
      </PluginSidebar>
    </>
  );
};

registerPlugin('seo-tab', {
  render: SeoTab,
  icon: 'admin-site',
});